import React from 'react';
import { useEffect, useState } from "react";
import AdminSectionGrid from 'components/AdminSectionGrid/AdminSectionGrid';
import AxiosFetch from 'library/hooks/AxiosFetch';
import { PostPlaceholder } from 'components/UI/ContentLoader/ContentLoader';
import { useParams } from 'react-router-dom';
import { Link } from 'react-router-dom';
import { Button } from 'antd';
import {
  ADMIN_OWNERS
} from 'settings/constant';
import { useNavigate } from 'react-router-dom';

export default function TokensByOwner() {

  const navigate = useNavigate();

  const [tokens, setTokens] = useState([]);
  let { address } = useParams();

  const fetchData = async () => {
    const url = '/getTokensByOwner';
    const body = {
      address: address,
    }
    const res = await AxiosFetch(url, body, "POST");
    if (res.data.success) {
      setTokens(res.data.data);
    }
  }

  useEffect(fetchData, []);

  return (
    <div>
      <Button type="primary" onClick={() => navigate(-1)} style={{marginBottom: "30px"}}>Back</Button>

      <AdminSectionGrid
        data={tokens}
        totalItem={tokens.length}
        columnWidth={[1 / 2, 1 / 2, 1 / 3, 1 / 4]}
        placeholder={<PostPlaceholder />}
        account=""
        updateTokenStaking = {fetchData}
      />
    </div>
  );
}
